import { MDBRow, MDBCol } from 'mdbreact';
import React from 'react';
import Graph from '../assets/graph.png';
import { BarChart, CartesianGrid, XAxis, YAxis, Tooltip, Legend, Bar, ResponsiveContainer, Text, Label } from 'recharts'

const data1 = [
  {
    name: 'Dabike et al. [1]',
    validation_WER: 23.33,
    test_WER: 19.60,
  },
  {
    name: 'Demirel et al. [2]',
    validation_WER: 17.70,
    test_WER: 14.96,
  },
  {
    name: 'Ahlback et al. [3]',
    validation_WER: 23.33,
    test_WER: 19.60,
  },
  {
    name: 'Ours',
    validation_WER: 13.26,
    test_WER: 14.56,
  },
]

const data2 = [
  {
    name: 'Audio',
    validation_WER: 14.04,
    test_WER: 21.90,
  },
  {
    name: 'Video',
    validation_WER: 47.91,
    test_WER: 68.45,
  },
  {
    name: 'Audio+Video',
    validation_WER: 13.30,
    test_WER: 17.56,
  },
  {
    name: 'Audio+Video+IMU',
    validation_WER: 0,
    test_WER: 0,
  },
]

const data3 = [
  {
    name: 'Addition',
    dev_WER: 15.01,
    test_WER: 23.30,
  },
  {
    name: 'Concatenation',
    dev_WER: 13.90,
    test_WER: 18.45,
  },
  {
    name: 'Self-attention',
    dev_WER: 13.48,
    test_WER: 18.67,
  },
  {
    name: 'Residual Cross Attention',
    dev_WER: 13.30,
    test_WER: 17.56,
  },
]

class Result extends React.Component {

  state = {

  }

  componentDidMount = () => {
  }

  render() {

    const styles = {
      landing: {
        padding: '10vh 15vw 0vh 15vw'
      },
      imageContainer: {
        display: 'flex',
        justifyContent: 'center',
        padding: '4vh 0vh'
      },
      image: {
        height: '45vh'
      }
    };

    return (
      <>
        <div style={styles.landing}>
          <MDBRow>

            <MDBCol md="12">
              <div className="header">
                {"Results"}
              </div>
            </MDBCol>

          </MDBRow>

          <MDBRow style={{ paddingTop: '2vh', paddingBottom: '2vh' }}>

            <MDBCol md="12">
              <div className="textHeader font-weight-bold">
                {"System Perfomance"}
              </div>
            </MDBCol>

            <MDBCol style={{ paddingTop: '1vh' }} md="12">
              <div className="textDescription">
                {"In the first figure, we show the performance of the proposed audio-only ALT system on DSing[1] dataset. Experiments show that our audio encoder based on wav2vec 2.0 has stronger performance than TDNN and its variants."}
              </div>
            </MDBCol>

            <MDBCol style={{ paddingTop: '1vh' }} md="12">
              <div className="textDescription">
                {"In the second figure, we show the performance of the proposed ALT system when consuming information from different combinations of modalities. The result shows that the more modalities the input contains, the higher performance it achieves."}
              </div>
            </MDBCol>

            <MDBCol style={{ paddingTop: '1vh' }} md="12">
              <div className="resultsText">
                {"[1]: Dabike, Gerardo Roa, and Jon Barker. \"Automatic Lyric Transcription from Karaoke Vocal Tracks: Resources and a Baseline System.\" Interspeech. 2019."}
              </div>
              <div className="resultsText">
                {"[2]: Demirel, Emir, Sven Ahlbäck, and Simon Dixon. \"Automatic lyrics transcription using dilated convolutional neural networks with self-attention.\" 2020 International Joint Conference on Neural Networks (IJCNN). IEEE, 2020."}
              </div>
              <div className="resultsText">
                {"[3]: Ahlbäck, Sven. \"MSTRE-Net: Multistreaming Acoustic Modeling for Automatic Lyrics Transcription.\" ISMIR2021, International Society for Music Information Retrieval. 2021."}
              </div>
            </MDBCol>
          </MDBRow>

          <MDBRow style={{ paddingTop: '2vh', paddingBottom: '2vh' }}>
            <MDBCol sm="12" md="6">
              <div style={{ paddingLeft: '7vw', display: 'flex', justifyContent: 'center' }}>
                {"Audio only on DSing"}
              </div>
              <BarChart width={570} height={300} data={data1}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="name" />
                <YAxis
                  allowDecimals={false}
                  type="number"
                  // domain={[0, 30]}
                  tickCount={6}
                />
                <Tooltip />
                <Legend verticalAlign="top" />
                <Bar dataKey="validation_WER" name="Validation WER" fill="#8884d8" />
                <Bar dataKey="test_WER" name="Test WER" fill="#82ca9d" />
              </BarChart>
              <div style={{ paddingLeft: '7vw', display: 'flex', justifyContent: 'center' }}>
                {"Method"}
              </div>
            </MDBCol>

            <MDBCol sm="12" md="6">
              <div style={{ paddingLeft: '7vw', display: 'flex', justifyContent: 'center' }}>
                {"A, V, A-V"}
              </div>
              <BarChart width={570} height={300} data={data2}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="name" />
                <YAxis
                  allowDecimals={false}
                  type="number"
                  // domain={[0, 30]}
                  tickCount={6}
                />
                <Tooltip />
                <Legend verticalAlign="top" />
                <Bar dataKey="validation_WER" name="Validation WER" fill="#8884d8" />
                <Bar dataKey="test_WER" name="Test WER" fill="#82ca9d" />
              </BarChart>
              <div style={{ paddingLeft: '7vw', display: 'flex', justifyContent: 'center' }}>
                {"Modality"}
              </div>
            </MDBCol>
          </MDBRow>



          <MDBRow style={{ paddingTop: '2vh', paddingBottom: '2vh' }}>

            <MDBCol md="12">
              <div className="textHeader font-weight-bold">
                {"Comparison of Feature Fusion"}
              </div>
            </MDBCol>

            <MDBCol style={{ paddingTop: '1vh' }} md="12">
              <div className="textDescription">
                {"We compare different feature fusion methods using the audio-video ALT system. It shows the effectiveness of the proposed Residual Cross Attention method."}
              </div>
            </MDBCol>

          </MDBRow>

          <MDBRow style={{ paddingTop: '2vh', paddingBottom: '2vh' }}>
            <MDBCol sm="12" md="6" className="offset-md-2">
              {/* <div style={{ paddingLeft: '7vw', display: 'flex', justifyContent: 'center' }}>
                {"Audio only on DSing"}
              </div> */}
              <BarChart width={570} height={300} data={data3}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="name" />
                <YAxis
                  allowDecimals={false}
                  type="number"
                  domain={[0, 30]}
                  tickCount={6}
                />
                <Tooltip />
                <Legend verticalAlign="top" />
                <Bar dataKey="dev_WER" name="Dev WER" fill="#8884d8" />
                <Bar dataKey="test_WER" name="Test WER" fill="#82ca9d" />
              </BarChart>
              <div style={{ paddingLeft: '7vw', display: 'flex', justifyContent: 'center' }}>
                {"Modality"}
              </div>
            </MDBCol>
          </MDBRow>

          <MDBRow style={{ paddingTop: '2vh', paddingBottom: '2vh' }}>

            <MDBCol md="12">
              <div className="textHeader font-weight-bold">
                {"Robustness"}
              </div>
            </MDBCol>
          </MDBRow>

          <MDBRow style={{ paddingTop: '2vh', paddingBottom: '2vh' }}>

            <MDBCol md="12">
              <div className="textHeader font-weight-bold">
                {"Case Study"}
              </div>
            </MDBCol>
          </MDBRow>

        </div>
      </>
    );
  }
}

export default Result;
